* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

img {
  max-width: 100%;
  display: block;
}

button {
  border: 1px solid;
  border-radius: 10px;
  padding: 10px 40px;
  font-size: 18px;
}

button:hover {
  color: #fafafa;
  background-color: #854481;
}

.timer {
  display: flex;
}

.field {
  flex-direction: column;
  align-items: center;
  margin-left: 10px;
  padding: 10px;
  display: flex;
}

.value {
  color: #8fa4bc;
  font-size: 40px;
  font-weight: bold;
}

.label {
  color: #8fa4bc;
  font-size: 20px;
  font-weight: bold;
}

